/* -------------------Main Application Container--------------- */

.main_form_container{
  display: flex;
  width: 52%;
  margin: 0 auto;
}

/* -------------------Task List Container---------------- */

.task_list_container {
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  padding: 5px;
  margin-top: 10px;
  background-color: #e0e0e0;
  border: whitesmoke;
  border-color: wheat;
  box-shadow: 0 8px 6px -6px black;
}

.task_list_row {
  border-radius: 40px;
  background-color: white;
  margin: 10px;
  height: 60px;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
}

.task_text {
  font-weight: 500;
  padding-left: 5px;
  font-size: 12.5pt;
}

.footer_container {
  align-items: center;
  margin-top: 20px;
  padding-left: 20px;
  font-weight: bolder;
  font-size: 11pt;
}

.error_message {
  padding-top: 10px;
  justify-content: center;
  text-align: center;
  font-weight: bolder;
  font-size: 13pt;
  color: red;
}

/* -----------------------Footer Container----------------------- */

.main_footer_container{
  display: flex; 
}

.total_count_container {
  width: 5vw;
  display: block;
  justify-content: center;
  padding-left: 25px;
}

.filter_buttons_container {
  width: 95vw;
  display: flow-root;
  text-align: center;
  padding-top: 8px;
  padding-right:100px;
  justify-content: center;
}

